import React, { createRef } from "react";
import { useMediaQuery } from "react-responsive";
import "./projects.scss";

import ProjectList from "../data/projects.json";

import { Modals, openModal, copyToClipboard, openImageModal } from "../components/modals";
import Tooltip from "../components/tooltip";
import HeaderItem from "../components/headerItem";
import TabsList from "../components/tabsList";

import HomeIcon from "../icon/home.svg";
import HireMeIcon from "../icon/hireme.svg";
import ChevronLeft from "../icon/cleft.svg";
import ChevronRight from "../icon/cright.svg";

const Header = () =>
{
	const isMobile = useMediaQuery({ query: "(max-width: 1224px)" });

	const toggleHeader = e =>
	{
		if (isMobile)
		{
			if (e.target.classList.contains("HeaderItems") || e.target.classList.contains("MainHeader"))
			{
				const items = document.getElementsByClassName("HeaderItems")[0].classList;

				if (items.contains("Collapsed"))
					items.remove("Collapsed");
				else
					items.add("Collapsed");
			}
		}
	};

	return (
		<header className="Header">
			<span className="MainHeader" onClick={toggleHeader}>My Other Projects</span>
			<span className={"HeaderItems" + (isMobile ? " Collapsed" : "")} onClick={toggleHeader}>
				<HeaderItem icon={HireMeIcon} content="Contact/Hire me!" action={() => window.location.assign("#/contact")}/>
				<HeaderItem icon={HomeIcon} content="Return to items page" action={() => window.location.assign("#/")}/>
			</span>
		</header>
	);
};

class Projects extends React.Component
{
	render()
	{
		return (
			<div>
				<Header/>

				<div className="Main">
					<div className="ProjectList">
						{ ProjectList.map(project => (<Project key={project.name} {...project}/>)) }
					</div>

					<Modals/>
				</div>

				<footer className="Footer">
					<span className="FooterItems">
						<span>Licenses - </span>
						<BasicLink link="https://fontawesome.com/license"/>
						<span> | Copyright Â© 2020 Israel Boone</span>
					</span>
				</footer>
			</div>
		)
	}
}

class Project extends React.Component
{
	render()
	{
		return (
			<div className="Project">
				<div className="ProjectName">{this.props.name} (<span className="Sub">{this.props.type}</span>)</div>
				<div className="ProjectDesc">{this.props.description}</div>

				<Slideshow images={this.props.images}/>

				{ this.props.buttons == null ? null : (
					<div className="ProjectButtons">
						{ this.props.buttons.map(button => (
							<Tooltip key={button.title} text={button.tooltip} hidden={button.tooltip == null}>
								<div className={"Button" + (button.disabled ? " Disabled" : "")} onClick={button.link == null ? null : () => window.open(button.link)}>
									<span>
										{ button.title }
									</span>
								</div>
							</Tooltip>
						)) }
					</div>
				) }
			</div>
		);
	}
}

class Slideshow extends React.Component
{
	constructor()
	{
		super();

		this.state = { current: 0 };
	}

	componentDidMount()
	{
		this.prev = () =>
		{
			if (this.state.current <= 0)
				this.state.current = this.props.images.length;
			
			this.setState({ current: this.state.current - 1 });
		};

		this.next = () =>
		{
			if (this.state.current >= this.props.images.length - 1)
				this.state.current = -1;

			this.setState({ current: this.state.current + 1 });
		};

		this.interval = setInterval(Slideshow.next, 5000);
	}

	componentWillUnmount()
	{
		if (this.interval != null)
		{
			clearInterval(this.interval);
		}
	}

	img(idx)
	{
		const img = this.props.images[this.state.current + idx];

		return img == null
			? null
			: (
				<div className={"Image" + (idx > 0 ? " Right" : idx < 0 ? " Left" : "")} style={{ backgroundImage: `url(${img})` }}/>
			);
	}

	render()
	{
		return (
			<div className="Slideshow">
				<div className="Images">
					{ this.props.images.map(img => (<div onClick={() => openImageModal(img)} key={this.props.images.indexOf(img)}
						className={"Image" + (this.state.current == this.props.images.indexOf(img) ? " Active" : "")} style={{ backgroundImage: `url(${img})` }}/>)) }
				</div>
				<div className="Buttons">
					<img className="Button Left" src={ChevronLeft} onClick={() => this.prev()}/>
					<img className="Button Right" src={ChevronRight} onClick={() => this.next()}/>
				</div>
			</div>
		);
	}
}

class BasicLink extends React.Component
{
	render()
	{
		return (
			<a className={this.props.className} href={this.props.link}>{this.props.text || this.props.link}</a>
		);
	}
}

export default Projects;