import React from "react";
import { Helmet } from "react-helmet";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import * as serviceWorker from "./serviceWorker";

ReactDOM.render(
	<React.StrictMode>
		<Helmet>
			<title>Tom's Discord Stuff</title>

			<meta property="og:type" content="website"/>
			<meta property="og:title" content="Tom's Discord Stuff"/>
			<meta property="og:description" content="Just a site with all of my Discord plugins, themes, and bots. Also has a page for my other active projects."/>
		</Helmet>

		<App/>
	</React.StrictMode>,
	document.getElementById("root")
);

serviceWorker.unregister();