import React, { createRef } from "react";
import "./tooltip.scss";

class Tooltip extends React.Component
{
	constructor()
	{
		super();

		this.target = createRef();
		this.self = createRef();

		this.state =
		{
			hidden: true
		};
	}

	render()
	{
		return (
			<React.Fragment>
				{React.cloneElement(
					this.props.children,
					{
						...this.props.children.props,
						onMouseEnter: () => this.setState({ hidden: false }),
						onMouseLeave: () => this.setState({ hidden: true }),
						ref: this.target
					}
				)}
				{ this.props.hidden ? null :
					(<div className={"Tooltip" + (this.state.hidden ? " Hidden" : "")} ref={this.self} style={
						this.target.current == null || this.self.current == null ? {} : {
							...this.props.style,
							left: (this.target.current.offsetLeft + (this.target.current.offsetWidth / 2)) - (this.self.current.offsetWidth / 2)
						}}>
						<div className="TooltipArrow"/>
						<span className="TooltipText">
							{this.props.text}
						</span>
					</div>) }
			</React.Fragment>
		);
	}
}

export default Tooltip;