import React from "react";
import "./tabsList.scss";

class TabsList extends React.Component
{
	constructor()
	{
		super();

		this.state = { active: "Plugins" };
	}

	fallback()
	{
		return (
			<div className="NoContentsMessage">
				There are currently no items here.
			</div>
		);
	}

	render()
	{
		const children = React.Children.toArray(this.props.children);

		return (
			<div>
				<div className="TabsList">
					{
						this.props.tabs.map(title =>
							(<span key={title} className={"TabButton" + (title == this.state.active ? " Active" : "")} onClick={() => this.setState({ active: title })}>
								<span className="TabButtonTitle">
									{title}
								</span>
								<div className="TabButtonBorder"/>
							</span>)
						)
					}
				</div>
				{ children.find(c => c.props.tab == this.state.active) || this.fallback() }
			</div>
		);
	}
}

export default TabsList;