import React, { createRef } from "react";
import { useMediaQuery } from "react-responsive";
import "./contact.scss";

import { Modals, openModal, closeModal, copyToClipboard, openImageModal } from "../components/modals";
import Tooltip from "../components/tooltip";
import HeaderItem from "../components/headerItem";
import TabsList from "../components/tabsList";

import HomeIcon from "../icon/home.svg";

let sent = false;

function ModalMsg(text)
{
	return (
		<div className="ClipboardModal" onClick={() => closeModal()}>
			<span>
				{ text }
			</span>
		</div>
	);
}

const fields = {};
function sendMessage()
{
	if (sent)
		return openModal(ModalMsg("Your message has already been sent, no need to send again!"));
		
	if (Object.keys(fields).length == 0 || Object.keys(fields).join("").length == 0 || fields["sender"] == null || fields["sender"].trim().length == 0)
		return openModal(ModalMsg("You must define contact info for me to reply to you!"));
	sent = true;
	
	const r = new XMLHttpRequest();

	r.onreadystatechange = () =>
	{
		if (r.readyState == 4 && r.status == 200)
			openModal(ModalMsg("Message successfully sent!\n\nI will respond as soon as possible, thank you!"));
		else if (r.readyState == 4)
			openModal(ModalMsg("Something went wrong! Please contact me on Discord to report this issue."));
	};

	openModal(ModalMsg("Sending message, please wait..."));

	r.open("POST", "https://postmail.invotes.com/send", true);
	r.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");

	const subject = encodeURIComponent(`${fields["budget"] || ""} ${fields["subject"] || "No subject."} by "${fields["sender"]}"`.trim());
	const text = encodeURIComponent(fields["body"] || "No body.");

	r.send(`access_token=89ekqieba771ts75tcj999jd&subject=${subject}&text=${text}`);
}

const Header = () =>
{
	const isMobile = useMediaQuery({ query: "(max-width: 1224px)" });

	const toggleHeader = e =>
	{
		if (isMobile)
		{
			if (e.target.classList.contains("HeaderItems") || e.target.classList.contains("MainHeader"))
			{
				const items = document.getElementsByClassName("HeaderItems")[0].classList;

				if (items.contains("Collapsed"))
					items.remove("Collapsed");
				else
					items.add("Collapsed");
			}
		}
	};

	return (
		<header className="Header">
			<span className="MainHeader" onClick={toggleHeader}>Contact Me</span>
			<span className={"HeaderItems" + (isMobile ? " Collapsed" : "")} onClick={toggleHeader}>
				<HeaderItem icon={HomeIcon} content="Return to last page" action={() => window.history.back()}/>
			</span>
		</header>
	);
};

class Projects extends React.Component
{
	render()
	{
		return (
			<div>
				<Header/>

				<div className="Main">
					<div className="Fields">
						<Field field="sender"
							title="How would you like me to reply?"
							note="This is what I will use to contact you back. This can be your email, your Discord tag, etc."
							placeholder="Example: Metalloriff ðŸ’•ðŸŒˆ#2891, itbchannel@gmail.com"/>

						<Field field="subject"
							title="What is the subject of your message?"
							note="Examples: Job Related, Idea, Bug Report, Just a message!"
							placeholder="Subject..."/>
						
						<Field field="budget"
							title="What is your budget?"
							note="This is only valid for job-related inquiries, ignore otherwise."
							placeholder="Example: $25"/>

						<Field field="body" multiline
							title="What would you like to say?"
							note="This is the main body of your message, express everything you're wanting to say here!"
							placeholder="Message..."/>
					</div>

					<div className="SendButton" onClick={() => sendMessage()}>Send</div>

					<Modals/>
				</div>

				<footer className="Footer">
					<span className="FooterItems">
						<span>Licenses - </span>
						<BasicLink link="https://fontawesome.com/license"/>
						<span> | Copyright Â© 2020 Israel Boone</span>
					</span>
				</footer>
			</div>
		)
	}
}

class Field extends React.Component
{
	onInput(e)
	{
		fields[this.props.field] = e.target.value;
	}

	render()
	{
		return (
			<div className="ContactField">
				<div className="Title">{this.props.title}</div>
				{ this.props.note == null ? null : (<div className="Title Note">{this.props.note}</div>) }

				{ this.props.multiline
					? (<textarea placeholder={this.props.placeholder} style={{ minHeight: 300 }} onChange={e => this.onInput(e)}/>)
					: (<input placeholder={this.props.placeholder} onInput={e => this.onInput(e)}/>) }
			</div>
		);
	}
}

class BasicLink extends React.Component
{
	render()
	{
		return (
			<a className={this.props.className} href={this.props.link}>{this.props.text || this.props.link}</a>
		);
	}
}

export default Projects;