import React from "react";
import "./headerItem.scss";

class Header extends React.Component
{
	render()
	{
		return (
			<div className="HeaderItemContainer" onClick={this.props.action}>
				<div className="HeaderItem">
					<span className="HeaderItemContent">{this.props.content}</span>
					<img className="HeaderItemIcon" src={this.props.icon} style={{
						width: (this.props.scale || 1) * 30
					}}/>
				</div>
			</div>
		);
	}
}

export default Header;